body {
  margin: 0;
  padding: 0;
  height: 100%;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html {
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%;
  min-height: 100%;
}




.App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
    height: 40vmin;
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

#root {
    background-color: beige;
    min-height: 100%;
    min-width: 100%;
}

.topLeft {
    border-radius: 400px 20px 150px 20px
}

.bottomRight {
    border-radius: 150px 20px 400px 20px

}

.bottomLeft {
    border-radius: 20px 150px 20px 400px

}

.topRight {
    border-radius: 20px 400px 20px 150px
}

.simonBtn {
    margin: 30px 15px;
    box-shadow: 0 20px 60px 0 #555
}

.simonBtn.hover {
    -webkit-filter: opacity(0.8);
            filter: opacity(0.8);
    border-color: black;
    box-shadow: 0 10px 30px 0px #555
}

.simonBtn.active {
    -webkit-filter: opacity(0.75);
            filter: opacity(0.75);
    border-color: black;
    box-shadow: 0 5px 30px 0px #555

}

.gameover {
    width: 600px;
    padding-top: 60px;
    background: red;
    height: 380px;
    border-radius: 70px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
}

.gameover p {
    margin-top: 0;
    margin-bottom: 0;
    font-size: 160px;
    font-family: 'Black Ops One', cursive;
    line-height: 120px;
    text-align: center;
    text-shadow: 5px 5px #000000;
    color: #ffffff;
    flex: 1 1;
}


/*noinspection CssNoGenericFontName*/
.restart {
    text-align: center;
    font-size: 30px;
    font-weight: bold;
    font-family: corbel;
    color: #ffffff;
    text-decoration: none;
    text-shadow: 3px 3px #000000;
    padding: 25px 110px 25px 110px;
    border: 1px solid #000000;
    border-radius: 20px;
    background: #006400;
    margin-right: 30px;
}

.score {
    line-height: 100px;
    font-size: 50px;
    font-style: italic;
    font-weight: 800;
}

@-webkit-keyframes animacao2 {
    from {
        -webkit-transform: scale(1);
    }
    to {
        -webkit-transform: scale(1.2);
    }
}

@keyframes animacao2 {
    from {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    to {
        -webkit-transform: scale(1.1);
                transform: scale(1.1);
    }
}

.restart:hover {
    -webkit-animation-name: animacao2;
    -webkit-animation-duration: 0.8s;
    -webkit-animation-timing-function: linear;
    -webkit-animation-iteration-count: infinite;
    -webkit-animation-direction: alternate;

    -moz-animation-name: animacao2;
    -moz-animation-duration: 0.8s;
    -moz-animation-timing-function: linear;
    -moz-animation-iteration-count: infinite;
    -moz-animation-direction: alternate;

    animation-name: animacao2;
    animation-duration: 0.8s;
    animation-timing-function: linear;
    animation-iteration-count: infinite;
    animation-direction: alternate;
}

